<p>使用L2TP，没有共享秘钥，mac上添加以下配置</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">cat</span> /etc/ppp/options
plugin L2TP.ppp
l2tpnoipsec
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>VPN连上了，ip是10.10.10.1</p>
